___CSS_LOADER_EXPORT___.push([module.id, `@media (max-width: 800px) {
  .menu-list {
    margin: 20px 0;
    font-size: 1.1rem;
  }

  .menu-list li {
    margin-left: 0;
  }

  .col {
    width: calc(100% / 2 - 16px);
  }
}

@media (max-width: 600px) {
  nav {
    padding: 0 46px;
  }

  .hero .title {
    font-size: 2.3rem;
  }

  .hero .subtitle {
    font-size: 1.5rem;
  }

  .menu-list {
    background-color: #c36a2d;
    fill-opacity: 0.5;
    position: fixed;
    top: 0;
    right: 0;
    width: 60%;
    height: 100%;
    margin-top: 0;
    padding: 64px 46px;
    flex-direction: column;
    align-items: flex-end;
    transform: translateX(100%);
    transition: transform 0.5s;
  }

  .menu-list li {
    padding: 10px 0;
  }

  .menu-list a {
    font-size: 2.1rem;
    color: #fcdc2a;
  }

  .open .menu-list {
    transform: translateX(0);
  }

  .menu-button {
    margin: auto 0;
    width: 48px;
    height: 48px;
    border: 0;
    border-radius: 3px;
    background-color: #fcdc2a;
    color: #114232;
    outline: none;
    font-size: 2rem;
    display: block;
    z-index: 2;
    cursor: pointer;
  }

  .menu i.fa-times,
  .menu.open i.fa-bars {
    display: none;
    animation: enter 0.8s;
  }

  .menu.open i.fa-times,
  .menu i.fa-bars {
    display: block;
    animation: enter 0.8s;
  }

  .menu-list a:hover,
  .menu-list a:active {
    font-weight: 800;
    transition: ease-out;
    transition-duration: 0.2s;
    border-bottom: 2px solid #114232;
    color: #fcdc2a;
  }

  .col {
    width: 100%;
  }
}
